import windowIcon from "raw-loader!../../assets/icons/window-maximize-solid.svg";
import vn from "../../locale/vn";

let nameDefault = vn["grapesjs-my-bootstrap"].Name;
const categoryName = nameDefault.Category.Layout;
const nameDefaultType = "default";

export const SectionBlock = (bm, label) => {
  bm.add("bt-section").set({
    label: `
            ${windowIcon}
            <div>${label}</div>
        `,
    category: categoryName,
    content: `<section class="section pt-50 pb-50">
        <div class="container">
            <div class="row">
              <div class="col"></div>
            </div>
        </div>
    </section>`,
  });
  bm.add("bt-section-2").set({
    label: `
            ${windowIcon}
            <div>${label} 2 cột</div>
        `,
    category: categoryName,
    content: `<section class="section pt-50 pb-50">
        <div class="container">
            <div class="row">
              <div class="col col-12 col-md-6"></div>
              <div class="col col-md"></div>
            </div>
        </div>
    </section>`,
  });
};

export default (domc) => {
  const defaultType = domc.getType(nameDefaultType);
  const defaultModel = defaultType.model;
  const defaultView = defaultType.view;

  domc.addType("bt-section", {
    model: defaultModel.extend({
      defaults: Object.assign({}, defaultModel.prototype.defaults, {
        "custom-name": "Section",
        tagName: "Section",
        resizable: {
          tl: 0,
          tc: 0,
          tr: 0,
          bl: 0,
          br: 0,
          bc: 1,
          cl: 0,
          cr: 0,
          step: 1,
          keyHeight: "min-height",
        },
        draggable: "#wrapper",
        droppable: true,
        traits: [{
            type: "skin",
            label: false,
          },
          {
            type: "visibility",
            label: false,
          },
          {
            type: "text_align",
            label: false,
          },
          {
            type: "section_options",
            label: false,
          },
        ],
        components: [{
            type: "bt-container",
          },
          {
            type: "bt-bg-section",
          },
        ],
      }),
    }, {
      isComponent(el) {
        if (
          el &&
          el.classList &&
          (el.tagName === "section" || el.classList.contains("section") || el.classList.contains("section-fluid"))
        ) {
          return {
            type: "bt-section",
          };
        }
      },
    }),
    view: defaultView,
  });
};