import imageIcon from "raw-loader!../../assets/icons/image-solid.svg";
import vn from "../../locale/vn";

let nameDefault = vn["grapesjs-my-bootstrap"].Name;
const labelName = nameDefault.Components.Media.Image;

export const ImageBlock = (bm, nameConfig) => {
  nameDefault = {
    ...nameDefault,
    ...nameConfig
  };
  const categoryName = nameDefault.Category.Media;

  bm.add("image", {
    label: `
            ${imageIcon}
            <div>${labelName}</div>
        `,
    category: categoryName,
    content: {
      type: "image",
    },
  });
};

export default (domComponent) => {
  const img_src_default = "https://dummyimage.com/800x500/999/222";
  const imageType = domComponent.getType("image");
  const model = imageType.model;
  const view = imageType.view;
  const type = "image";

  domComponent.addType(type, {
    model: model.extend({
      defaults: Object.assign({}, model.prototype.defaults, {
        "custom-name": labelName,
        tagName: "img",
        resizable: {
          tl: 1,
          tc: 1,
          tr: 1,
          cl: 1,
          cr: 1,
          bl: 1,
          bc: 1,
          br: 1,
          minDim: 16,
        },
        attributes: {
          src: img_src_default,
        },
        classes: ["img-fluid"],
        traits: [{
            type: "text",
            label: "Chú thích",
            name: "alt",
          },
          {
            type: "box_options",
            label: false,
          },
        ],
      }),
    }, {
      isComponent: function (el) {
        if (el && el.tagName === "IMG") {
          return {
            type: type,
          };
        }
      },
    }),
    view: view,
  });
};