import listHeaderBlocks from "./headers/Headers";
import listDescriptionsBlocks from './descriptions/Descriptions';

const loadTemplateBlock = (bm, listBlocks, categoryName) => {
  let index = 0;
  listBlocks.forEach(block => {
    bm.add(categoryName + index).set({
      label: block.Label,
      category: categoryName,
      content: `${block.Content}`,
    });
    index++;
  });
};

export default (blockManager, config = {}) => {
  const bm = blockManager;

  loadTemplateBlock(bm, listHeaderBlocks, "Headers");
  loadTemplateBlock(bm, listDescriptionsBlocks, "Descriptions")
};