import FuncVideo from '../../../common/function/FuncVideo';
import ConfigTraits from "../../config/ConfigTraits";

const traitClasses = ConfigTraits.VideoOptions.Classes;
const traitHtml = ConfigTraits.VideoOptions.HTML;
const traitName = ConfigTraits.VideoOptions.Name;

export default (editor, config = {}) => {
    const tm = editor.TraitManager;
    tm.addType(traitName, {
        events: {
            change: "onChange", // trigger parent onChange method on input change
            "change .cs-trait-video-option input.url-source": "handleChange",
        },
        handleChange(event) {
            var $elInput = $(event.target);
            let sectionComp = this.target;

            let valueUrl = $elInput.val();
            let videoComps = sectionComp.find(".embed-responsive-item");

            if (videoComps && videoComps[0]) {
                let videoComp = videoComps[0];
                let videoObj = FuncVideo.parseVideo(valueUrl);
                if (videoObj) {
                    switch (videoObj.type) {
                        case 'youtube':
                            videoComp.set({
                                videoId: videoObj.id,
                                provider: 'yt',
                            });
                            break;
                        case 'vimeo':
                            videoComp.set({
                                videoId: videoObj.id,
                                provider: 'vi',
                            });
                            break;
                        default:
                            break;
                    }
                } else {
                    console.warn('Không tìm thấy video');
                }
            }
        },
        templateInput: "",
        createInput({
            trait
        }) {
            var md = this.model;
            var opts = md.get("options") || [];
            var input = $(traitHtml).get()[0];
            return input;
        },
        /**
         *
         * @param {*} param0
         */
        onUpdate({
            elInput,
            component
        }) {
            let videoComps = component.find(".embed-responsive-item");
            if (videoComps && videoComps[0]) {
                let videoComp = videoComps[0];
                let linkVideo = videoComp.get('src');
                $(elInput).find('input').val(linkVideo);
            }
        },
    });
};