import ConfigComponents from "../config/ConfigComponents";

const componentName = ConfigComponents.Type.Svg;

export default (dc) => {
    const textType = dc.getType(componentName);
    const textModel = textType.model;
    const textView = textType.view;

    dc.addType(componentName, {
        model: textModel.extend({
            defaults: {
                ...textModel.prototype.defaults,
                "custom-name": "Icon",
                droppable: false,
                highlightable: false,
                resizable: {
                    tl: 1,
                    tr: 1,
                    bl: 1,
                    br: 1,
                    tc: 0,
                    bc: 0,
                    cl: 0,
                    cr: 0,
                    ratioDefault: 1,
                    minDim: 16,
                },
            },
        }, {
            isComponent(el) {
                var result = "";
                if (el.tagName === "svg") {
                    result = {
                        type: "svg"
                    };
                }
                return result;
            },
        })
    });
};