import _ from "underscore";
import windowIcon from "raw-loader!../../../assets/icons/button.svg";
const traitClasses =
    "full-height boxed margin margin-x2 margin-x3 overall section-line section-line-container";

//import _s from 'underscore.string';
export default (editor, config = {}) => {
    const tm = editor.TraitManager;
    tm.addType("section_options", {
        events: {
            change: "onChange", // trigger parent onChange method on input change
            "click .cs-traits-section div.item": "handleClick",
        },
        handleClick(event) {
            var $elClick = $(event.target);
            let $elOptionClick = $elClick.attr("data-value") ?
                $elClick :
                $elClick.parents("[data-value]");
            var dataValue = $elOptionClick.attr("data-value");
            let sectionComp = this.target;
            if (dataValue) {
                if ($elOptionClick.hasClass("selected")) {
                    sectionComp.removeClass(dataValue);
                    $elOptionClick.removeClass("selected");
                } else {
                    $elOptionClick.siblings().removeClass("selected");
                    $elOptionClick.addClass("selected");

                    let classes = traitClasses;
                    sectionComp.removeClass(classes);
                    sectionComp.addClass(dataValue);
                }
            }
        },
        templateInput: "",
        createInput({
            trait
        }) {
            var md = this.model;
            var opts = md.get("options") || [];
            var input = $(
                `<div class="trait-setting cs-traits-section"><label>Kiểu Section</label>
        <div class="wrapper d-flex flex-wrap justify-content-between">
            <div data-value="full-height" class="item d-flex flex-column align-items-center">
                ${windowIcon}
                <label>Full height</label></div>
            <div data-value="boxed" class="item d-flex flex-column align-items-center">
                ${windowIcon}
                <label>Boxed</label></div>
            <div data-value="margin" class="item d-flex flex-column align-items-center">
                ${windowIcon}
                <label>Margin</label></div>
            <div data-value="margin-x2" class="item d-flex flex-column align-items-center">
                ${windowIcon}
                <label>Margin x2</label></div>
            <div data-value="margin-x3" class="item d-flex flex-column align-items-center">
                ${windowIcon}
                <label>Margin x3</label></div>
            <div data-value="overall" class="item d-flex flex-column align-items-center">
                ${windowIcon}
                <label>Overall</label></div>
            <div data-value="section-line" class="item d-flex flex-column align-items-center">
                ${windowIcon}
                <label>Separator screen</label></div>
            <div data-value="section-line-container" class="item d-flex flex-column align-items-center">
                ${windowIcon}
                <label>Separator
                    content</label></div>
        </div>
    </div>`
            ).get()[0];
            return input;
        },
        onUpdate({
            elInput,
            component
        }) {
            const classes = component.getClasses();
            var optionClasses = traitClasses.split(" ");
            optionClasses.forEach((optionClass) => {
                if (optionClass && classes.includes(optionClass)) {
                    $(elInput).find(`[data-value="${optionClass}"]`).addClass('selected');
                    return;
                }
            });
        },

        onEvent({
            elInput,
            component,
            event
        }) {
            var classes = this.model.get("options").map((opt) => opt.value);
            for (let i = 0; i < classes.length; i++) {
                if (classes[i].length > 0) {
                    var classes_i_a = classes[i].split(" ");
                    for (let j = 0; j < classes_i_a.length; j++) {
                        if (classes_i_a[j].length > 0) {
                            component.removeClass(classes_i_a[j]);
                        }
                    }
                }
            }
            const value = this.model.get("value");
            if (value.length > 0 && value != "GJS_NO_CLASS") {
                const value_a = value.split(" ");
                for (let i = 0; i < value_a.length; i++) {
                    component.addClass(value_a[i]);
                }
            }
            component.em.trigger("component:toggled");
        },
    });
};