import _ from "underscore";
import ConfigTraits from "../../config/ConfigTraits"


const traitClasses = "bt-text-shadow bt-text-thin-shadow bt-text-hard-shadow bt-text-pushed-shadow bt-text-isometric-shadow bt-text-glow bt-text-3d bt-text-isometric bt-text-anaglyphic";
//import _s from 'underscore.string';
export default (editor, config = {}) => {
    const tm = editor.TraitManager;
    tm.addType("text_effect", {
        events: {
            change: "onChange", // trigger parent onChange method on input change
            "click .cs-text-effect [data-value]": "handleClick",
        },
        handleClick(event) {
            var $elClick = $(event.target);
            let $elOptionClick = $elClick.attr("data-value") ?
                $elClick :
                $elClick.parents("[data-value]");
            var dataValue = $elOptionClick.attr("data-value");
            let sectionComp = this.target;
            if (dataValue) {
                if ($elOptionClick.hasClass("selected")) {
                    sectionComp.removeClass(dataValue);
                    $elOptionClick.removeClass("selected");
                } else {
                    $elOptionClick.siblings().removeClass("selected");
                    $elOptionClick.addClass("selected");

                    let classes = traitClasses;
                    sectionComp.removeClass(classes);
                    sectionComp.addClass(dataValue);
                }
            }
        },
        templateInput: "",
        createInput({
            trait
        }) {
            var md = this.model;
            var opts = md.get("options") || [];

            //   var input = document.createElement("select");
            var input = $(ConfigTraits.TextEffect.HTML).get()[0];
            return input;
        },
        onUpdate({
            elInput,
            component
        }) {
            const classes = component.getClasses();
            var optionClasses = traitClasses.split(" ");
            optionClasses.forEach((optionClass) => {
                if (optionClass && classes.includes(optionClass)) {
                    $(elInput).find(`[data-value="${optionClass}"]`).addClass('selected');
                    return;
                }
            });
        },

        onEvent({
            elInput,
            component,
            event
        }) {
            var classes = this.model.get("options").map((opt) => opt.value);
            for (let i = 0; i < classes.length; i++) {
                if (classes[i].length > 0) {
                    var classes_i_a = classes[i].split(" ");
                    for (let j = 0; j < classes_i_a.length; j++) {
                        if (classes_i_a[j].length > 0) {
                            component.removeClass(classes_i_a[j]);
                        }
                    }
                }
            }
            const value = this.model.get("value");
            if (value.length > 0 && value != "GJS_NO_CLASS") {
                const value_a = value.split(" ");
                for (let i = 0; i < value_a.length; i++) {
                    component.addClass(value_a[i]);
                }
            }
            component.em.trigger("component:toggled");
        },
    });
};