import default_blocks from "./common/DefaultBlocks";
import default_labels from "./common/DefaultLabels";
import default_categories from "./common/DefaultCategories";
import loadCssString from "./common/LoadCss";

import loadComponents from "./components";
import loadTraits from "./traits";
import loadDevices from "./devices";
import vi from "./locale/vn";

import ManagerIcons from './manager-icons/view/ManagerIcons';
// import loadAssets from './manager-assets/index';

const loadCss = (editor) => {
  editor.Config.canvasCss += loadCssString;
};

export default (editor, opts = {}) => {
  const opts_blocks = opts.blocks || {};
  const opts_labels = opts.labels || {};
  const opts_categories = opts.blockCategories || {};
  delete opts["blocks"];
  delete opts["labels"];
  delete opts["blockCategories"];

  let options = {
    i18n: {
      Name: vi["grapesjs-my-bootstrap"].Name
    },
    ...{
      blocks: Object.assign(default_blocks, opts_blocks),
      labels: Object.assign(default_labels, opts_labels),
      blockCategories: Object.assign(default_categories, opts_categories),
      optionsStringSeparator: "::",
      gridDevices: true,
      gridDevicesPanel: false,
      classNavigation: "nav",
      classTabPanes: "tab-content",
      classTabPane: "tab-pane",
      classTab: "nav-item",
    },
    ...opts,
  };

  // Load i18n files
  editor.I18n.addMessages({
    vi,
    ...options.i18n,
  });

  // Add components
  loadTraits(editor, options);
  loadComponents(editor, options);
  loadDevices(editor, options);
  loadCss(editor, options);

  //Manager icons
  let managerIcons = new ManagerIcons();
  // loadAssets();
};