import buttonIcon from "raw-loader!../../assets/icons/window-maximize-solid.svg";
import ConfigComponents from "../config/ConfigComponents";
import vn from "../../locale/vn";

let nameDefault = vn["grapesjs-my-bootstrap"].Name;

const componentName = ConfigComponents.Type.Button;
const labelName = nameDefault.Components.Basic.Button;

export const ButtonBlock = (bm, nameConfig) => {
  nameDefault = { ...nameDefault, ...nameConfig };
  const categoryName = nameDefault.Category.Basic;

  bm.add(componentName, {
    label: `${buttonIcon}<div>${labelName}</div>`,
    category: categoryName,
    content: '<a class="btn btn-primary">Button</a>',
  });
};

export default (dc) => {
  const textType = dc.getType("text");
  const textModel = textType.model;
  const textView = textType.view;

  dc.addType(componentName, {
    model: textModel.extend(
      {
        defaults: {
          ...textModel.prototype.defaults,
          "custom-name": labelName,
          droppable: false,
          tagName: "a",
          attributes: {
            href: "#",
          },
          classes: ["btn"],
          traits: [
            {
              type: "button_type",
              label: false,
            },
          ],
        },
      },
      {
        isComponent(el) {
          if (el && el.classList && el.classList.contains("btn")) {
            return {
              type: componentName,
            };
          }
        },
      }
    ),
    view: textView.extend({}),
  });
};
