import _ from "underscore";
import ConfigTraits from "../../config/ConfigTraits";

const configTextAlign = ConfigTraits.TextAlign;
const traitClasses = configTextAlign.Classes;
//import _s from 'underscore.string';
export default (editor, config = {}) => {
  const tm = editor.TraitManager;
  tm.addType("text_align", {
    events: {
      change: "onChange", // trigger parent onChange method on input change
      "click .cs-trait-text-align button": "handleClick",
    },
    //!TODO
    handleClick(event) {
      const selected = editor.getSelected();
      if (!selected) {
        return;
      }
      var $elClick = $(event.target);
      let $elOptionClick = $elClick.attr("data-value")
        ? $elClick
        : $elClick.parents("[data-value]");
      var dataValue = $elOptionClick.attr("data-value");
      let sectionComp = this.target;
      //compute the current screen size (bootstrap semantic)
      const docWidth = sectionComp.getEl().getRootNode().body.offsetWidth;
      let currentSize = "";
      if (docWidth >= 992) {
        currentSize = "-lg";
      } else if (docWidth >= 768) {
        currentSize = "-md";
      }
      if (dataValue) {
        if (!$elOptionClick.hasClass("selected")) {
          $elOptionClick.siblings().removeClass("selected");
          $elOptionClick.addClass("selected");
          //   $elOptionClick.addClass("selected");
          let optionClasses = traitClasses.split(" ");
          let optionClassSizes = optionClasses.map(function (value) {
            return `text${currentSize}-${value}`;
          });
          let classes = optionClassSizes.join(" ");
          sectionComp.removeClass(classes);
          sectionComp.addClass(`text${currentSize}-${dataValue}`);
        }
      }
    },
    templateInput: "",
    createInput({ trait }) {
      var md = this.model;
      var opts = md.get("options") || [];

      //   var input = document.createElement("select");
      var input = $(configTextAlign.HTML).get()[0];
      return input;
    },
    //!TODO
    onUpdate({ elInput, component }) {
      const textAlign = $(component.getEl()).css("text-align");
      let $traitSelect = $(elInput).find(`[data-value="${textAlign}"]`);
      $traitSelect.siblings().removeClass("selected");
      $traitSelect.addClass("selected");
    },
  });
};
