import fontIcon from "raw-loader!../../assets/icons/font-solid.svg";
import vn from "../../locale/vn";

const typeName = "text";
let nameDefault = vn["grapesjs-my-bootstrap"].Name;

export const TextBlock = (bm, nameConfig = {}) => {
  let nameDefault = { ...nameDefault, ...nameConfig };

  const labelName = nameDefault.Components.Typography.Text;
  const contentText = nameDefault.Components.Typography.TextContent;
  const categoryName = nameDefault.Category.Typography;

  bm.add(typeName, {
    label: `
            ${fontIcon}
            <div>${labelName}</div>
        `,
    category: categoryName,
    activeOnRender: 1,
    content: {
      type: typeName,
      content: contentText,
      highlightable: false,
    },
  });
};

export default (domc) => {
  const defaultType = domc.getType("default");
  const defaultModel = defaultType.model;
  const textType = domc.getType("text");
  const textView = textType.view;

  const labelName = nameDefault.Components.Typography.Text;

  domc.addType(typeName, {
    model: defaultModel.extend(
      {
        defaults: Object.assign({}, defaultModel.prototype.defaults, {
          type: typeName,
          "custom-name": labelName,
          droppable: false,
          editable: true,
          highlightable: false,
          traits: [
            {
              type: "drag_mod",
              label: false,
            },
            {
              type: 'skin',
              label: false,
            },
            {
              type: 'visibility',
              label: false,
            },
            {
              type: "text_align",
              label: false,
            },
            {
              type: "text_effect",
              label: false,
            },
          ],
        }),
      },
      {
        isComponent(el) {
          if (el && el.dataset && el.dataset.bsText) {
            return {
              type: typeName,
            };
          }
        },
      }
    ),
    view: textView,
    toHTML() {
      this.trigger("sync:content", {
        silent: 1,
      });
      return Component.prototype.toHTML.apply(this, arguments);
    },
  });
};
