//Component Layout
import Container, { ContainerBlock } from "./components/layout/BtContainer";
import Row, { RowBlock } from "./components/layout/BtRow";
import Column, { ColumnBlock } from "./components/layout/BtColumn";
import Section, { SectionBlock } from "./components/layout/BtSection";
import BgSection from "./components/layout/BtBgSection";

//Media
import Image, { ImageBlock } from "./components/media/BtImage";
import EmbedVideo from "./components/media/video/BtEmbed";
import Video, { VideoBlock } from "./components/media/video/BtVideo";

//Typography
import Text, { TextBlock } from "./components/typography/BtText";
import Header, { HeaderBlock } from "./components/typography/BtHeader";

//Basic
import Button, { ButtonBlock } from "./components/components/BtButton";
import Icon, { IconBlock } from "./components/components/BtIcon";
import Svg from './components/components/Svg';

//Specific
import { MapBlock } from "./components/specific/Map";

//Load Template Block
import loadTemplateBlock from "./components/templates/Templates";

export default (editor, config = {}) => {
  const c = config;
  const nameConfig = config.i18n.Name;

  const domComponents = editor.DomComponents;
  const blocks = c.blocks;
  const bm = editor.BlockManager;
  const cats = c.blockCategories;

  //Load Template Block
  if (cats.template) {
    loadTemplateBlock(bm);
  }

  // TODO: Rebuild the default component and add utility settings to it (border, bg, color, etc)
  if (cats.basic) {
    if (blocks.text) {
      TextBlock(bm, nameConfig);
      Text(domComponents);
    }
    if (blocks.header) {
      HeaderBlock(bm, nameConfig);
      Header(domComponents);
    }
  }

  // TODO: LAYOUT
  if (cats.layout) {
    if (blocks.section) {
      SectionBlock(bm, c.labels.section);
      Section(domComponents, editor);
    }
    if (blocks.bg_section) {
      BgSection(domComponents, editor);
    }
    if (blocks.container) {
      ContainerBlock(bm, c.labels.container);
      Container(domComponents);
    }
    if (blocks.row) {
      RowBlock(bm, c.labels.row);
      Row(domComponents);
    }
    if (blocks.column) {
      ColumnBlock(bm, c.labels.column);
      Column(domComponents, editor);
    }
  }

  //TODO: Media
  if (cats.media) {
    if (blocks.image) {
      ImageBlock(bm, nameConfig);
      Image(domComponents);
    }
    if (blocks.video) {
      EmbedVideo(domComponents);
      VideoBlock(bm, nameConfig);
      Video(domComponents);
    }
  }

  //TODO: Component
  if (cats.basic) {
    if (blocks.button) {
      ButtonBlock(bm, nameConfig);
      Button(domComponents);
    }
    if (blocks.icon) {
      IconBlock(bm, nameConfig);
      Icon(domComponents);
      Svg(domComponents);
    }
    //Map
    MapBlock(bm, nameConfig);
  }
};