import windowIcon from "raw-loader!../../assets/icons/window-maximize-solid.svg";
import vn from "../../locale/vn";

let nameDefault = vn["grapesjs-my-bootstrap"].Name;
const nameDefaultType = "default";
const categoryName = nameDefault.Category.Layout;

export const ContainerBlock = (bm, label) => {
  bm.add("bt-container").set({
    label: `
            ${windowIcon}
            <div>${label}</div>
        `,
    category: categoryName,
    content: {
      type: "bt-container",
    },
  });
};

export default (domc) => {
  const defaultType = domc.getType(nameDefaultType);
  const defaultModel = defaultType.model;
  const defaultView = defaultType.view;

  domc.addType("bt-container", {
    model: defaultModel.extend({
      defaults: Object.assign({}, defaultModel.prototype.defaults, {
        "custom-name": "Container",
        tagName: "div",
        classes: ["container"],
        resizable: {
          tl: 0,
          tc: 0,
          tr: 0,
          bl: 0,
          br: 0,
          bc: 1,
          cl: 1,
          cr: 1,
          keyWidth: "max-width",
          step: 1,
          keyHeight: "min-height",
        },
        droppable: true,
        traits: [{
            type: "class_select",
            options: [{
                value: "container",
                name: "Fixed",
              },
              {
                value: "container-fluid",
                name: "Fluid",
              },
            ],
            label: "Width",
          },
          {
            type: "button",
            // ...
            text: "Click me",
            full: false, // Full width button
            command: (editor) => alert("Hello"),
            // or you can just specify the Command ID
            command: "some-command",
            label: "Option",
          },
        ].concat(defaultModel.prototype.defaults.traits),
      }),
    }, {
      isComponent(el) {
        if (
          el &&
          el.classList &&
          (el.classList.contains("container") ||
            el.classList.contains("container-fluid"))
        ) {
          return {
            type: "bt-container",
          };
        }
      },
    }),
    view: defaultView,
  });
};