//Vendor
import loadClassSelect from './vendor/ClassSelect';
import loadContent from './vendor/Content';

//My
import loadBoxOptions from "./box/BoxOptions";
import loadSectionOptions from "./section/SectionOptions";
import loadTextAlign from "./text/TextAlign";
import loadTextEffect from "./text/TextEffect";

import loadButtonType from "./button/ButtonType";

//Media
import loadVideoOptions from './media/VideoOptions';

export default (editor, config = {}) => {
    //Vendor
    loadClassSelect(editor, config);
    loadContent(editor, config);

    //Load traits
    loadBoxOptions(editor, config);
    loadSectionOptions(editor, config);
    loadTextAlign(editor, config);
    loadTextEffect(editor, config);

    loadButtonType(editor, config);

    //Media
    loadVideoOptions(editor, config);
};