export default `
      /* Layout */

      .gjs-dashed .container, .gjs-dashed .container-fluid,
      .gjs-dashed .row,
      .gjs-dashed .col, .gjs-dashed [class^="col-"] {
        min-height: 2rem;
      }
      .gjs-dashed .w-100 {
        min-height: .25rem;
        background-color: rgba(0,0,0,0.1);
      }
      .gjs-dashed img {
        min-width: 25px;
        min-height: 25px;
        // background-color: rgba(0,0,0,0.5);
      }

      /* Components */

      .gjs-dashed .btn-group,
      .gjs-dashed .btn-toolbar {
        padding-right: 1.5rem;
        min-height: 1.5rem;
      }
      .gjs-dashed .card,
      .gjs-dashed .card-group, .gjs-dashed .card-deck, .gjs-dashed .card-columns {
        min-height: 1.5rem;
      }
      .gjs-dashed .collapse {
        display: block;
        min-height: 1.5rem;
      }
      .gjs-dashed .dropdown {
        display: block;
        min-height: 1.5rem;
      }
      .gjs-dashed .dropdown-menu {
        min-height: 1.5rem;
        display: block;
      }
    `;
