import imageIcon from "raw-loader!../../assets/icons/map.svg";
import vn from "../../locale/vn";

let nameDefault = vn["grapesjs-my-bootstrap"].Name;
const labelName = nameDefault.Components.Specific.Map;

export const MapBlock = (bm, nameConfig) => {
  nameDefault = { ...nameDefault, ...nameConfig };
  const categoryName = nameDefault.Category.Media;

  bm.add("bt-map", {
    label: `
            ${imageIcon}
            <div>${labelName}</div>
        `,
    category: categoryName,
    content: {
      name: "Bản đồ",
      type: "map",
      editable: false,
      resizable: true,
      droppable: false,
      address: "Duy Tân,Hà Nội, Việt Nam",
      zoom: "15",
      style: {
        height: "350px",
        width: "100%",
      },
      traits: [
        {
          label: "Địa chỉ",
          name: "address",
          placeholder: "eg. London, UK",
          changeProp: 1,
        },
        {
          type: "select",
          label: "Loại",
          name: "mapType",
          changeProp: 1,
          options: [
            { value: "q", name: "Roadmap" },
            { value: "w", name: "Satellite" },
          ],
        },
        {
          label: "Thu phóng",
          name: "zoom",
          type: "range",
          min: "1",
          max: "20",
          changeProp: 1,
        },
      ],
    },
  });
};
