const FuncCommon = {
  /**
   * Lấy class cho màn hình hiện tại - sử dụng bootstrap
   * @param {string|selector} selector
   * CreateBy dvhung1 24/08/2020
   */
  getCurrentSizeClass: function (selector) {
    const docWidth = selector.getRootNode().body.offsetWidth;
    let currentSize = "";
    if (docWidth >= 992) {
      currentSize = "lg";
    } else if (docWidth >= 768) {
      currentSize = "md";
    }
    return currentSize;
  },
};
export default FuncCommon;
