const nameType = "bt-bg-section";
const nameDefaultType = "default";

export default (domc) => {
  const defaultType = domc.getType(nameDefaultType);
  const defaultModel = defaultType.model;
  const defaultView = defaultType.view;

  domc.addType(nameType, {
    model: defaultModel.extend({
      defaults: Object.assign({}, defaultModel.prototype.defaults, {
        "custom-name": "Bg Section",
        traits: [],
        resizable: false,
        draggable: false,
        droppable: false,
        badgable: false,
        highlightable: false,
        layerable: false,
        selectable: false,
        hoverable: false,
        classes: ["bg-wrap"],
        stylable: [],
        content: "",
      }),
    }, {
      isComponent(el) {
        if (el && el.classList && el.classList.contains("bg-wrap")) {
          return {
            type: nameType,
          };
        }
      },
    }),
    view: defaultView.extend({
      // init() {
      //   const comps = this.model.get("components");
      //   comps.reset();
      //   debugger
      //   comps.add(`<div class="bg"></div>`);
      // },
    }),
  });
};