import fontIcon from "raw-loader!../../assets/icons/heading-solid.svg";
import vn from "../../locale/vn";

let nameDefault = vn["grapesjs-my-bootstrap"].Name;
const labelName = nameDefault.Components.Typography.Header;
const typeName = "Header";

export const HeaderBlock = (bm, nameConfig) => {
  nameDefault = { ...nameDefault, ...nameConfig };
  const contentText = nameDefault.Components.Typography.TextContent;
  const categoryName = nameDefault.Category.Typography;

  bm.add(typeName, {
    label: `
            ${fontIcon}
            <div>${labelName}</div>
        `,
    category: categoryName,
    activeOnRender: 1,
    content: {
      type: typeName,
      content: contentText,
      highlightable: false,
    },
  });
};

export default (domComponent) => {
  const textType = domComponent.getType("text");
  const textModel = textType.model;
  const textView = textType.view;

  domComponent.addType(typeName, {
    model: textModel.extend(
      {
        defaults: Object.assign({}, textModel.prototype.defaults, {
          type: typeName,
          "custom-name": labelName,
          tagName: "h1",
          droppable: false,
          editable: true,
          highlightable: false,
          traits: [
            {
              type: "drag_mod",
              label: false,
            },
            {
              type: 'skin',
              label: false,
            },
            {
              type: 'visibility',
              label: false,
            },
            {
              type: "text_align",
              label: false,
            },
            {
              type: "text_effect",
              label: false,
            },
          ],
        }),
      },
      {
        isComponent(el) {
          if (el && ["H1", "H2", "H3", "H4", "H5", "H6"].includes(el.tagName)) {
            return {
              type: typeName,
            };
          }
        },
      }
    ),
    view: textView,
  });
};
