import videoIcon from "raw-loader!../../../assets/icons/youtube-brands.svg";
import vn from "../../../locale/vn";

const typeName = "bt-embed-responsive";
const typeEmbedResponsiveVideo = "bt-embed-video";

export const VideoBlock = (bm, nameConfig) => {
  let nameDefault = vn["grapesjs-my-bootstrap"].Name;
  nameDefault = {
    ...nameDefault,
    ...nameConfig,
  };
  const labelName = nameDefault.Components.Media.Video;
  const categoryName = nameDefault.Category.Media;

  bm.add(typeName, {
    label: `
            ${videoIcon}
            <div>${labelName}</div>
        `,
    category: categoryName,
    content: {
      type: typeEmbedResponsiveVideo,
    },
  });
};

export default (domComponent) => {
  const videoType = domComponent.getType("video");
  const model = videoType.model;
  const view = videoType.view;

  domComponent.addType(typeName, {
    model: model.extend(
      {
        defaults: Object.assign({}, model.prototype.defaults, {
          "custom-name": "Video",
          resizable: false,
          droppable: false,
          draggable: false,
          copyable: false,
          highlightable: false,
          selectable: false,
          hoverable: false,
          provider: "vi",
          videoId: '448011635',
          src: "https://player.vimeo.com/video/448011635?",
          classes: ["embed-responsive-item"],
        }),
      },
      {
        isComponent: function (el) {
          if (el && el.className === "embed-responsive-item") {
            return {
              type: typeName,
            };
          }
        },
      }
    ),
    view: view,
  });
};


