import buttonIcon from "raw-loader!../../assets/icons/circle-solid.svg";
import ConfigComponents from "../config/ConfigComponents";
import vn from "../../locale/vn";

const componentName = ConfigComponents.Type.Icon;
export const IconBlock = (bm, nameConfig) => {
  let nameDefault = vn["grapesjs-my-bootstrap"].Name;
  nameDefault = {
    ...nameDefault,
    ...nameConfig,
  };
  const labelName = nameDefault.Components.Basic.Icon;
  const categoryName = nameDefault.Category.Basic;

  bm.add(componentName, {
    label: `${buttonIcon}<div>${labelName}</div>`,
    category: categoryName,
    content: {
      type: componentName,
    },
  });
};

export default (dc) => {
  const textType = dc.getType("default");
  const textModel = textType.model;
  const textView = textType.view;

  dc.addType(componentName, {
    model: textModel.extend({
      defaults: {
        ...textModel.prototype.defaults,
        "custom-name": "Icon",
        droppable: false,
        highlightable: false,
        resizable: {
          tl: 1,
          tr: 1,
          bl: 1,
          br: 1,
          tc: 0,
          bc: 0,
          cl: 0,
          cr: 0,
          ratioDefault: 1,
          minDim: 16,
        },
        style: {
          width: "64px",
          height: "64px",
        },
        tagName: "div",
        // classes: ["icon-v1"],
        content: `<svg data-gjs-type=" svg" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"
            style="pointer-events:none">
            <path data-gjs-type="svg-in" draggable="true"
                d="M7.5 21.5l1.35-1.34 3.81 3.81L12 24C5.71 24 .56 19.16.05 13h1.5c.36 3.76 2.7 6.94 5.95 8.5m9-19l-1.35 1.34L11.34.03 12 0c6.29 0 11.44 4.84 11.95 11h-1.5c-.36-3.76-2.7-6.93-5.95-8.5M6 17c0-2 4-3.1 6-3.1s6 1.1 6 3.1v1H6v-1m9-8c0 1.66-1.34 3-3 3s-3-1.34-3-3 1.34-3 3-3 3 1.34 3 3z"
                id="i2duhb"></path>
            </svg>`,
      },
    }, {
      isComponent(el) {
        // if (el && el.classList && el.classList.contains("icon-v1")) {
        //   return {
        //     type: componentName,
        //   };

        // }
        var result = "";
        if (el.tagName === "svg") {
          result = {
            type: "svg"
          };
        }
        return result;
      },
    }),
    view: textView.extend({
      events: {
        dblclick: "onActive",
      },
      /**
       * Mở dialog lựa chọn Icons
       * @param  {Object}  ev  Event
       * @private
       * */
      onActive(ev) {
        ev && ev.stopPropagation();
        console.log("open-dialog Icon ");
        console.log({
          ev
        });
      },
    }),
  });
};